<template>
    <div class="notes__item">
        <div class="notes__item_top" :class="{active: !view}">
            <h2 class="notes__item_top_title">{{ note.title }}</h2>
            <p class="notes__item_top_date">{{ note.date }}</p>
        </div>
        <p class="notes__item_text">{{ note.text }}</p>
        <div class="notes__item_btns">
            <button class="notes__item_btns_btn purple" @click="$emit('change', note)">
                <img src="@/assets/images/edit.svg" alt="">
                <span>РЕДАКТИРОВАТЬ</span>
            </button>
            <button class="notes__item_btns_btn red" @click="$emit('delNote', note)">
                <img src="@/assets/images/del.svg" alt="">
                <span>Удалить</span>
            </button>
        </div>
    </div>
</template>

<script>
    export default {
        props:{
            note:{
                type: Object
            },
            view:{
                type: Boolean
            }
        }
    }
</script>
