<template>
  <div class="wrapper">
      <Navbar @setSearch="searchValue = $event"></Navbar>
      <Notes 
        :notes="filterNotes"
        @delNote="delNote"
        @change="change"
      ></Notes>
      <Transition name="modal">
        <Modal
          :isEdit="isEdit"
          :editNote="editNote"
          v-if="isModalOpen"
          @close="isModalOpen = false, editNote = null"
          @addNote="addNote"
          @changeNote="changeNote"
        ></Modal>
      </Transition>
      <button class="add" v-if="!isModalOpen" @click="isModalOpen = true, isEdit = false">
        <img src="@/assets/images/edit.svg" alt="">
      </button>
  </div>
</template>

<script>
import Modal from './components/Modal.vue';
import Navbar from './components/Navbar.vue';
import Notes from './components/Notes.vue';

  export default {
    components: {
    Navbar,
    Notes,
    Modal
},
  data(){
    return{
      searchValue: '',
      isEdit: false,
      isModalOpen: false,
      editNote: null,
      notes:[]
    }
  },
  watch:{
    notes:{
      handler(newValue){
        localStorage.notes = JSON.stringify(this.notes)
      },
      deep: true
    }
  },
  mounted() {
    this.getNotes()
  },
  computed: {
    filterNotes(){
      return this.searchValue ? this.notes.filter(note => note.title.toLowerCase().includes(this.searchValue.toLowerCase()) ) : this.notes
    }
  },
  methods: {
    addNote(note){
      this.notes.push(note)
    },
    delNote(note){
      this.notes = this.notes.filter(el => el.id != note.id)
    },
    change(note){
      this.isModalOpen = this.isEdit = true
      this.editNote = note 
    },
    changeNote(newNote){
      this.notes.forEach((note,i) => note.id == newNote.id ? this.notes.splice(i,1, newNote) : '' )
    },
    getNotes(){
      if(localStorage.notes){
        this.notes = JSON.parse(localStorage.notes)
      }      
    },
    
  },
}
</script>

