<template>
    <div class="modal" @click="closeModal">
        <div class="modal__block" @click.stop>
            <h2 class="modal__block_title">
                {{ isEdit ? 'Изменить заметку' : 'Добавить заметку'}}
            </h2>
            <div class="modal__block_inputs">
                <label>
                    <span>Title</span>
                    <input v-model="title" placeholder="Title" type="text">
                </label>
                <label>
                    <span>Content</span>
                    <textarea v-model="text" placeholder="Content"></textarea>
                </label>
            </div>
            <div class="modal__block_btns">
                <button class="modal__block_btns_btn red" @click="closeModal">Отмена</button>
                <button v-if="!isEdit" class="modal__block_btns_btn purple" @click="addNote">Добавить</button>
                <button v-if="isEdit" class="modal__block_btns_btn purple" @click="changeNote">Изменить</button>
            </div>
        </div>
    </div>
</template>

<script>
import { v4 as uuidv4 } from 'uuid';
    export default {
        data(){
            return{
                title: '',
                text: ''
            }
        },
        props: {
            isEdit: {
                type: Boolean
            },
            editNote: {
                type: Object
            }
        },
        created() {
            this.title = this.editNote?.title
            this.text = this.editNote?.text
        },
        methods: {
            closeModal(){
                this.$emit('close')
                this.title = this.text = ''
            },
            addNote(){
                if(this.text.length > 2 && this.title.length > 2){
                    const note = {
                        id: uuidv4(),
                        title: this.title,
                        text: this.text,
                        date: new Date().toLocaleDateString()
                    }
                this.$emit('addNote', note)
                this.closeModal()
                }
            },
            changeNote(){
                if(this.title.length > 2 && this.text.length > 2){
                    const newNote = {
                        id: this.editNote.id,
                        title: this.title,
                        text: this.text,
                        date: new Date().toLocaleDateString()
                    }
                    this.$emit('changeNote', newNote)
                    this.closeModal()
                }
            }
        },
    }
</script>
