<template>
<header class="header">
    <nav class="header__nav" v-if="header">
        <button class="header__nav_lang">Ru</button>
        <h1 class="header__nav_title">Заметки</h1>
        <button class="header__nav_search" @click="header = false">
            <img src="@/assets/images/search.svg" alt="">
        </button>
    </nav>
    <nav class="header__search" v-else>
        <button class="header__search_back" @click="header = true, search= '' ">
            <img src="@/assets/images/back.svg" alt="">
        </button>
        <input v-model="search" type="text" class="header__search_input" placeholder="Поиск...">
        <button class="header__search_clear" @click="search= ''">
            <img src="@/assets/images/close.svg" alt="">
        </button>
    </nav>
</header>
</template>

<script>
    export default {
        data() {
            return {
                search: '',
                header: true
            }
        },
        watch: {
            search(newVal) {
                console.log(newVal);
                this.$emit('setSearch', newVal)
            }
        }
    }
</script>

